import { StyledTableCell, StyledTableRow } from "components/templates/Tables";
import {
  Table,
  TableRow,
  TableBody,
  TableHead,
  TableContainer,
} from "@mui/material";
import Stack from "@mui/material/Stack";

import { BsTrashFill } from "react-icons/bs";
import { AiOutlineEdit } from "react-icons/ai";

import TableLoadingWrapper from "components/templates/TableLoadingWrapper";
import Button from "components/atoms/Button";
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import AuthService from "services/auth.service";
import { useEffect } from "react";

import { MODAL } from "redux/slices/modal";
import { modalActions } from "redux/slices/modal";

const AccountManagementList = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const users = useAppSelector((state) => state.auth.users);
  const loading = useAppSelector((state) => state.auth.loading);

  useEffect(() => {
    AuthService.getUsers(dispatch);
  }, [dispatch]);

  console.log("our user are=======", users);

  return (
    <div>
      <TableContainer>
        <Table
          aria-label="customized table"
          sx={{
            minWidth: "100%",
            borderSpacing: "0 10px",
            borderBottomWidth: "0px",
            borderCollapse: "separate",
          }}
        >
          <TableLoadingWrapper
            container
            coloumns={4}
            loading={loading}
            message="No user available"
            length={users.length}
          >
            <TableHead>
              <TableRow>
                <StyledTableCell>First Name</StyledTableCell>
                <StyledTableCell>Last Name</StyledTableCell>
                <StyledTableCell>Email</StyledTableCell>
                <StyledTableCell>Roles</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {users.map((user: any, i: number) => (
                <StyledTableRow key={i}>
                  <StyledTableCell>{user.firstName}</StyledTableCell>
                  <StyledTableCell>{user.lastName}</StyledTableCell>
                  <StyledTableCell>{user.email}</StyledTableCell>
                  <StyledTableCell>{user.role}</StyledTableCell>
                  <StyledTableCell></StyledTableCell>

                  <StyledTableCell align="right">
                    <Stack direction="row" spacing={2} justifyContent="right">
                      <Button
                        // size="large"
                        variant="contained"
                        color="primary"
                        disableElevation
                        onClick={() =>
                          dispatch(
                            modalActions.openModal({
                              width: "500px",
                              type: MODAL.CONFIRMATION_FORM,
                              data: {
                                id: user._id,
                                type: MODAL.DELETE_USER,
                                heading: "Delete USER",
                                message:
                                  "Do you really want to delete this USER?",
                              },
                            })
                          )
                        }
                        sx={{ minWidth: "auto" }}
                      >
                        <BsTrashFill />
                      </Button>
                      <Button
                        // size="large"
                        variant="contained"
                        color="secondary"
                        disableElevation
                        onClick={() => navigate(`/update-Account/${user._id}`)}
                        sx={{ minWidth: "auto" }}
                      >
                        <AiOutlineEdit />
                      </Button>
                      <Button
                        // size="large"
                        variant="contained"
                        color="success"
                        disableElevation
                        onClick={() => navigate(`/points`)}
                        sx={{ minWidth: "auto" }}
                      >
                        POINT USAGE
                      </Button>
                    </Stack>
                  </StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </TableLoadingWrapper>
        </Table>
      </TableContainer>
    </div>
  );
};

export default AccountManagementList;
