import { Container, Stack } from "@mui/material";
import Button from "components/atoms/Button";
import Banner from "components/templates/Banner";
import BasicCard from "components/templates/BasicCard";
import { useNavigate } from "react-router-dom";
import AccountManagementList from "./AccountManagementLIst";
import AccountManagementFilter from "./AccountManagementFilter";
import { useAppSelector } from "redux/hooks";

const AccountManagement = () => {
  const navigate = useNavigate();
  const users = useAppSelector((state) => state.auth.users);

  return (
    <Container maxWidth="lg">
      <Banner heading={`Account Management (${users.length})`}>
        <Stack direction="row" spacing={2}>
          <Button
            color="secondary"
            variant="contained"
            sx={{ borderRadius: "25px" }}
          >
            Export
          </Button>
          <Button
            color="primary"
            variant="contained"
            sx={{ borderRadius: "25px" }}
            onClick={() => navigate("/add-newAccount")}
          >
            + Add New
          </Button>
        </Stack>
      </Banner>
      <BasicCard>
        <AccountManagementFilter />
      </BasicCard>
      <BasicCard>
        <AccountManagementList />
      </BasicCard>
    </Container>
  );
};

export default AccountManagement;
